import { gsap } from 'gsap';
import Complex from 'complex.js';
import Circle from './circle.js';

var dist = function dist(x1, y1, x2, y2) {
  return Math.sqrt(Math.pow(x2 - x1, 2) + Math.pow(y2 - y1, 2));
};

var hasTween = function hasTween(element) {
  return gsap.isTweening(element.position);
};

var isAsleep = function isAsleep(bod) {
  return bod.sleeping;
};

function meshToCircle(mesh) {
  var c = new Circle(mesh.geometry.parameters.radius, new Complex(mesh.position.x, mesh.position.z), 0);
  return c;
}

function meshesToCircles(meshesL) {
  var circlesL = [];
  meshesL.forEach(function (mesh) {
    return circlesL.push(meshToCircle(mesh));
  });
  return circlesL;
}

function isColliding(circA, circB) {
  var d = dist(circA.position.x, circA.position.z, circB.position.x, circB.position.z);
  var rSum = circA.geometry.parameters.radius + circB.geometry.parameters.radius;
  return d < rSum + 5;
}

export { dist, hasTween, meshesToCircles, isAsleep, isColliding };