function createSphere(scene, _radius, x, y, z, wireF) {
  var radius = Math.abs(_radius);

  if (radius === undefined || x === undefined || y === undefined || z === undefined) {
    console.log('Undefined inputs to createSphere');
    return;
  }

  var geometry = new SphereGeometry(radius, 32, 32);
  var material = new MeshStandardMaterial({
    color: 'black',
    wireframe: false,
    wireframeLinewidth: 0.1,
    morphTargets: true
  });
  var sphere = new Mesh(geometry, material);
  sphere.position.set(x, y, z);
  scene.add(sphere);
  return sphere;
}

function createMatcapSphere(scene, _radius, x, y, z) {
  var radius = Math.abs(_radius);

  if (radius === undefined || x === undefined || y === undefined || z === undefined) {
    console.log('Undefined inputs to createMatcapSphere');
    return;
  }

  var geometry = new SphereGeometry(radius, 32, 32);
  var material = new MeshMatcapMaterial({
    color: 0x005500
  });
  var sphere = new Mesh(geometry, material);
  sphere.position.set(x, y, z);
  scene.add(sphere);
  return sphere;
}

function createWireframeSphere(scene, _radius, x, y, z) {
  var radius = Math.abs(_radius);

  if (radius === undefined || x === undefined || y === undefined || z === undefined) {
    console.log('Undefined inputs to createWireframeSphere');
    return;
  }

  var geometry = new SphereGeometry(radius, 32, 32);
  var material = new MeshPhongMaterial({
    color: 'white',
    wireframe: true,
    emissive: 'pink',
    shininess: 50
  });
  var sphere = new Mesh(geometry, material);
  sphere.position.set(x, y, z);
  scene.add(sphere);
  return sphere;
}

function createStaticBox(scene, size, position, rotation, color) {
  var ToRad = 0.0174532925199432957;
  var geometry = new BoxGeometry(size[0], size[1], size[2]);
  var material = new MeshBasicMaterial({
    color: color
  });
  var mesh = new Mesh(geometry, material);
  mesh.position.set(position[0], position[1], position[2]);
  mesh.rotation.set(rotation[0] * ToRad, rotation[1] * ToRad, rotation[2] * ToRad);
  scene.add(mesh);
  mesh.castShadow = true;
  mesh.receiveShadow = true;
  return mesh;
}

function createShaderBox(scene, size, position, rotation, uniforms, vertexShader, fragmentShader) {
  var ToRad = 0.0174532925199432957;
  var geometry = new BoxGeometry(size[0], size[1], size[2]);
  var material = new ShaderMaterial({
    uniforms: uniforms,
    vertexShader: vertexShader,
    fragmentShader: fragmentShader
  });
  var mesh = new Mesh(geometry, material);
  mesh.position.set(position[0], position[1], position[2]);
  mesh.rotation.set(rotation[0] * ToRad, rotation[1] * ToRad, rotation[2] * ToRad);
  scene.add(mesh);
  mesh.castShadow = true;
  mesh.receiveShadow = true;
  return mesh;
}
/* generates a sequence of spheres following the path of the circle
 * on the xy-plane or xz-plane */


function createShapeAlong2DPath(scene, circle, createShapeFunc, xzPlane, //xzPlane => y = 0
rPadding, shapeSize) {
  var r = Math.abs(circle.r) + rPadding;
  var x_0 = circle.z.re;
  var y_0 = circle.z.im;
  var yOffset = 200;
  var degStep = 15;

  for (var deg = 0; deg < 360; deg += degStep) {
    var x = x_0 + r * Math.cos(deg);

    var _y = y_0 + r * Math.sin(deg);

    var y = xzPlane ? 0 : _y + yOffset;
    var z = xzPlane ? _y : 0;
    createShapeFunc(scene, shapeSize, x, y, z);
  }
}

export { createSphere, createWireframeSphere, createMatcapSphere, createStaticBox, createShaderBox, createShapeAlong2DPath };