var colors = {
  white: 0xffffff,
  black: 0x0,
  turquoise: 0x42f5b6,
  pink: 0xffd1dc
};
var cameraPositions = {
  topDown: [0, 600, 0],
  Angled: [0, 600, 600],
  horizontal: [0, 0, 600]
};
var options = {
  backgroundColor: "lightblue",
  antialias: true,
  cameraPosition: cameraPositions.horizontal,
  grid: {
    size: 1000,
    divisions: 50
  },
  fog: {
    near: 0.5,
    far: 1000
  }
}; //----------------------------------
