// animation constants

var origin = new Vector3(0, 0, 0);
var Yaxis = new Vector3(0, 1, 0);
var groundInfo = {
  size: [3000, 5, 3000],
  pos: [0, -80, 0]
};
/* information for circle seeds */

var gap = 400;
var adjFactor = 1.5;
var collisionPadding = 5;
var centerCircleStartPos = new Vector3(0, 0, 350); // used once in init

var centerCircleStartR = 10;
var sideCircleStartLeftDest = new Vector3(centerCircleStartPos.x - centerCircleStartR / adjFactor, centerCircleStartPos.y, centerCircleStartPos.z - centerCircleStartR / adjFactor);
var sideCircleStartRightDest = new Vector3(centerCircleStartPos.x + centerCircleStartR / adjFactor, centerCircleStartPos.y, centerCircleStartPos.z - centerCircleStartR / adjFactor);
var leftCircleStartPos = new Vector3(-gap, 0, -gap);
var rightCircleStartPos = new Vector3(gap, 0, -gap);
var centerCircleAnimationFrom = new Vector3(0, 0, 800); // shader

var uniformDefault = new Vector3(4 * gap, 0, 4 * gap);
export { colors, options, gap, groundInfo, collisionPadding, adjFactor, origin, Yaxis, centerCircleStartPos, centerCircleStartR, centerCircleAnimationFrom, sideCircleStartLeftDest, sideCircleStartRightDest, leftCircleStartPos, rightCircleStartPos, uniformDefault };